import axios from 'axios';
import { NotificationManager } from 'react-notifications';

const ToggleFavourite = async (ID, toggle) => {
  if (toggle) {
    await axios
      .post('api/add_favourite', { ID })
      .then((res) => {
        NotificationManager.success(
          'Successfully Added Into Favourites',
          'Successful!',
          2000
        );
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
  } else {
    await axios
      .post('api/del_favourite', { ID })
      .then((res) => {
        NotificationManager.success(
          'Successfully Removed From Favourites',
          'Successful!',
          2000
        );
      })
      .catch((err) => {
        console.log(err);
      });
  }
};

export default ToggleFavourite;
