import React, { Component } from 'react';
import axios from 'axios';
import { MDBDataTable, MDBContainer, MDBRow, MDBCol } from 'mdbreact';
import ToggleFavourite from './add-fav';
import { CPagination, CSelect } from '@coreui/react';

const columns = [
  {
    label: 'Id',
    field: 'id',
    sort: 'asc',
  },
  {
    label: 'Domain',
    field: 'domain',
    sort: 'asc',
  },
  {
    label: 'Metrics',
    field: 'metrics',
    sort: 'asc',
  },
  {
    label: 'Words',
    field: 'words',
    sort: 'asc',
  },
  {
    label: 'Sentiment_score',
    field: 'sentiment_score',
    sort: 'asc',
  },
  {
    label: 'Add_Fav',
    field: 'add_fav',
    sort: 'asc',
  },
];

export default class CustomTables extends Component {
  state = {
    is_favourite: true,
    togglesearch: false,
    pageNo: 1,
    size: 5,
    pagesAmount: 1,
    keyword: null,
    columns: [...columns],
    rows: [],
    domain: null,
    length: null,
    alphanumeric: null,
    numeric: null,
    hyphenated: null,
    tld: null,
    wordcount: null,
    words: null,
    score: null,
  };
  toggleSearch = () => {
    this.setState({
      pageNo: 1,
      size: 5,
      pagesAmount: 1,
      keyword: null,
      domain: null,
      metrics: null,
      words: null,
      score: null,
      togglesearch: !this.state.togglesearch,
    });
  };
  onPageChange = (e) => {
    this.setState({ pageNo: e }, () => {
      this.getData(this.state);
    });
  };
  onChange = (e) => {
    const { name, value } = e.target;
    this.setState(
      {
        [name]: value.trim(),
      },
      () => {
        this.getData(this.state);
      }
    );
  };
  getData = async (args) => {
    await axios
      .post('api/get_domains', {
        ...args,
      })
      .then((res) => {
        console.log(res);
        let data = res.data.data;
        let pages = res.data.pages;
        let rows = [];
        if (res.data.data.length > 0) {
          data.map((data) => {
            rows.push({
              id: data._id,
              domain: data.domain,
              metrics: (
                <>
                  <span>alphanumeric: {data.metrics.alphanumeric}</span>
                  <hr className='p-0 m-0' />
                  <span>hyphenated :{data.metrics.hyphenated}</span>
                  <hr className='p-0 m-0' />
                  <span>
                    length:
                    {data.metrics.length}
                  </span>
                  <hr className='p-0 m-0' />
                  ​​ <span>numeric: {data.metrics.numeric}</span>
                  <hr className='p-0 m-0' />
                  <span>
                    tld:
                    {data.metrics.tld}
                  </span>
                  <hr className='p-0 m-0' />
                  ​​ <span>wordcount{data.metrics.wordcount}</span>
                  <hr className='p-0 m-0' />
                </>
              ),
              words: (
                <>
                  {data.words.map((w, key) => {
                    return (
                      <span key={key}>
                        {w} <hr className='p-0 m-0' />
                      </span>
                    );
                  })}
                </>
              ),
              sentiment_score: data.sentiment_score,
              add_fav: (
                <button
                  className={
                    !data.is_favourite
                      ? 'btn btn-success btn-sm'
                      : 'btn btn-danger btn-sm'
                  }
                  onClick={() => {
                    ToggleFavourite(data._id, !data.is_favourite);
                    this.getData(this.state);
                  }}>
                  {!data.is_favourite ? <> add fav</> : <>remove fav</>}
                </button>
              ),
            });
          });
          console.log('Setting state . .. ', rows);
          this.setState({
            ...this.state,
            rows,
            pagesAmount: pages,
          });
        } else {
          this.setState({
            ...this.state,
            rows: [],
            pageNo: 1,
            pagesAmount: 1,
          });
        }
      })
      .catch((err) => {
        console.log(err);
        this.setState({ ...this.state, rows: [], pageNo: 1, pagesAmount: 1 });
      });
  };
  componentDidMount() {
    this.getData(this.state);
  }
  render() {
    return (
      <>
        <MDBContainer className='d-flex text-align-center '>
          <header className='text-align-center'>
            <h1>FAVOURITE COMPONENT</h1>
          </header>
        </MDBContainer>
        <MDBContainer className='mt-5'>
          <MDBRow>
            <MDBCol>
              <button
                style={{ height: '35px' }}
                className='btn btn-sm btn-success border-outline-green'
                onClick={this.toggleSearch}>
                Toggle Search
              </button>
            </MDBCol>
            <MDBCol size='9'>
              <input
                style={{ height: '35px' }}
                placeholder='SuperSearch'
                disabled={this.state.togglesearch}
                name='keyword'
                onChange={this.onChange}
                type='text'
                className='form-control form-control-sm'
              />
            </MDBCol>
            <MDBCol>
              <CSelect
                custom
                name='size'
                placeholder='No of Entries'
                onChange={this.onChange}
                id='ccmonth'>
                <option value='5'>5</option>
                <option value='10'>10</option>
                <option value='20'>20</option>
                <option value='30'>30</option>
                <option value='40'>40</option>
                <option value='50'>50</option>
                <option value='60'>60</option>
                <option value='70'>70</option>
                <option value='80'>80</option>
                <option value='90'>90</option>
                <option value='100'>100</option>
                <option value='500'>500</option>
                <option value='1000'>1000</option>
              </CSelect>
            </MDBCol>
          </MDBRow>
          <MDBRow className='mt-4'>
            <MDBCol>
              <input
                style={{ height: '35px' }}
                placeholder='Domain'
                disabled={!this.state.togglesearch}
                name='domain'
                onChange={this.onChange}
                type='text'
                className='form-control form-control-sm'
              />
            </MDBCol>
            <MDBCol>
              <input
                style={{ height: '35px' }}
                placeholder='m.length'
                disabled={!this.state.togglesearch}
                name='length'
                onChange={this.onChange}
                type='number'
                className='form-control form-control-sm'
              />
            </MDBCol>
            <MDBCol>
              <input
                style={{ height: '35px' }}
                placeholder='m.alphanumeric'
                disabled={!this.state.togglesearch}
                name='alphanumeric'
                onChange={this.onChange}
                type='number'
                className='form-control form-control-sm'
              />
            </MDBCol>
            <MDBCol>
              <input
                style={{ height: '35px' }}
                placeholder='m.hyphenated'
                disabled={!this.state.togglesearch}
                name='hyphenated'
                onChange={this.onChange}
                type='number'
                className='form-control form-control-sm'
              />
            </MDBCol>
            <MDBCol>
              <input
                style={{ height: '35px' }}
                placeholder='m.tld'
                disabled={!this.state.togglesearch}
                name='tld'
                onChange={this.onChange}
                type='text'
                className='form-control form-control-sm'
              />
            </MDBCol>
            <MDBCol>
              <input
                style={{ height: '35px' }}
                placeholder='m.wordcount'
                disabled={!this.state.togglesearch}
                name='wordcount'
                onChange={this.onChange}
                type='number'
                className='form-control form-control-sm'
              />
            </MDBCol>
            <MDBCol>
              <input
                style={{ height: '35px' }}
                placeholder='Words'
                disabled={!this.state.togglesearch}
                name='words'
                onChange={this.onChange}
                type='text'
                className='form-control form-control-sm'
              />
            </MDBCol>
            <MDBCol>
              <input
                style={{ height: '35px' }}
                placeholder='Sentimental score'
                disabled={!this.state.togglesearch}
                name='score'
                onChange={this.onChange}
                type='number'
                className='form-control form-control-sm'
              />
            </MDBCol>
          </MDBRow>
          <MDBContainer className='heavy-rain-gradient rounded hoverable mt-5'>
            <MDBDataTable
              id='divToPrint'
              className='text-dark black-text p-2 strong'
              searching={false}
              responsive
              paging={false}
              striped
              theadColor='dark'
              small
              noBottomColumns
              hover
              data={this.state}
            />
          </MDBContainer>

          <CPagination
            align='center'
            activePage={this.state.pageNo}
            pages={this.state.pagesAmount}
            onActivePageChange={this.onPageChange}
          />
        </MDBContainer>
      </>
    );
  }
}
